.btn {
    border-radius: 3.1rem;
    padding: 1.6rem 2.5rem;

    @include media-breakpoint-up(md) {
        padding: 2rem 3.3rem;
    }

    @include media-breakpoint-up(lg) {
        padding: 2.3rem 4.7rem 2.2rem;
    }

    &-narrow{
        border: 0;
        font-size: 1.4rem;
        line-height: 1.6rem;
        padding: 1.05rem 2.83rem;
        @include media-breakpoint-up(md){
            font-size: 1.6rem;
            line-height: 1.7rem;
        }
        @include media-breakpoint-up(lg){
            padding: 1.05rem 1.47rem;
        }
    }

    &-primary {
        background-color: $primary;
        color: $white;
        border: 0;
    }

    

    &-dark {
        background-color: $yellow;
        border: 0;
        color: $secondary;
        &:hover{
            color: $secondary !important;
            background-color: $yellow;
        }
    }

    &-with-arrow {
        padding: 0;
    }

    &-bubble {
        position: relative;
        overflow: hidden;

        strong {
            font-weight: $font-weight-semibold;
            z-index: 9;
            position: relative;
        }
        

        span {
            position: absolute;
            width: 25%;
            height: 100%;
            background-color: $dark-green;
            transform: translateY(150%);
            border-radius: 50%;
            left: calc((var(--n) - 1) * 25%);
            transition: 0.5s;
            transition-delay: calc((var(--n) - 1) * 0.1s);
            z-index: 1;

            &:nth-child(1) {
                --n: 1;
            }

            &:nth-child(2) {
                --n: 2;
            }

            &:nth-child(3) {
                --n: 3;
            }

            &:nth-child(4) {
                --n: 4;
            }
        }


        &:hover {
            color: $white;

            span {
                transform: translateY(0) scale(2);
            }
        }
        .bg-dark-green &{
            &:hover{
                color: $dark-green;
            }
        }
        &.btn-outline{
            &:hover{
                // background-color: $primary;
            }
            span{
                background-color: $primary;
            }
        }
    }

    &-outline {
        border: 0.3rem solid $primary;
        color: $primary;
        border-radius: 4rem;
        background-color: transparent;
        @include media-breakpoint-up(md) {
            padding: 1.7rem 3.1rem;
        }
    
        @include media-breakpoint-up(lg) {
            padding: 2.3rem 4.7rem 2.2rem;
        }

        .bg-dark-green &{
            &:hover{
                color: $white;
            }
        }
    }

    &-secondary {
        background-color: $secondary;
        color: $white;
    }
    &-small{
        border: transparent;
        background-color: transparent;
        width: 5.1rem;
        height: 5.1rem;
        i{
            font-size: 5.1rem;
            &::before{
                color: $yellow;
            }
        }
    }
    &-circle{
        padding: 0;
        border: transparent;
        background-color: transparent;
        width: 4rem;
        height: 4rem;
        @include media-breakpoint-up(lg){
            width: 6rem;
            height: 6rem;
        }
        img{
            width: 100%;
            height: 100%;
        }
    }
}


.bg-dark-green,
.bullettext-l-image-r--curvebg-dark-green {
    .btn-bubble {
        span {
            background-color: $light;

        }
        &:hover {
            color: $primary;
        }
    }
}
